import React from 'react';
import logo from '../../assets/img/logo.png';
import { Link } from 'react-router-dom';

import './Header.scss';

function Header() {
    return (
        <header>
            <nav className="navbar undermenu">
                <div className="navbar-container">
                    <div className="navbar-inner">
                        <div className="logo">
                            <Link to="/"><img src={logo} alt="Логотип" /></Link>
                        </div>
                        <div className="menu">
                            <ul className="menu-list">
                                <li className="menu-item"><a href="#catalog" className="menu-link">Каталог</a></li>
                                <li className="menu-item"><a href="#about" className="menu-link">О нас</a></li>
                                <li className="menu-item"><a href="#contacts" className="menu-link">Контакты</a></li>
                            </ul>
                        </div>
                        <div className="navbar-contacts">
                            <div className="phone-call">
                                <a href="tel:+998909367766" className="hide-sm"><i className="fas fa-mobile"></i><span>+998(90) 936-77-66</span></a>
                                <a href="tel:+998909367766"><i className="fas fa-mobile"></i><span>+998(90) 936-77-66</span></a>
                            </div>
                            <div className="social-icons">
                                <a href="any.site.com"><i className="fab fa-telegram"></i></a>
                                <a href="any.site.com"><i className="fab fa-whatsapp"></i></a>
                            </div>
                        </div>
                        <div className="toggle-btn">
                            <span className="lines line-1"></span>
                            <span className="lines line-2"></span>
                            <span className="lines line-3"></span>
                        </div>
                    </div>
                </div>
            </nav>
        </header>
    )
}

export default Header;
