import React from 'react';
import './Advantages.scss';

function Advantages() {
    return (
        <div className="advantages">
            <div className="container">
                <div className="row">
                    <div className="advantages-item">
                        <div className="advantages-icon">
                            <i className="fa fa-truck"></i>
                        </div>
                        <h3 className="advantages-title">Доставка по всей России</h3>
                        <p className="advantages-text">Мы производим доставки по всей России в самые краткие сроки.</p>
                    </div>
                    <div className="advantages-item">
                        <div className="advantages-icon">
                            <i className="fa fa-tag" aria-hidden="true"></i>
                        </div>
                        <h3 className="advantages-title">Cистема скидок для оптовиков</h3>
                        <p className="advantages-text">Делаем скидок для оптовых магазинов и заказов больших партий.</p>
                    </div>
                    <div className="advantages-item">
                        <div className="advantages-icon">
                            <i className="fa fa-credit-card" aria-hidden="true"></i>
                        </div>
                        <h3 className="advantages-title">Гибкие условии оплаты</h3>
                        <p className="advantages-text">Удобный и легкий способ оплаты через банковские переводы или наличными</p>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Advantages;
