import React from 'react';
import './ContactForm.scss';

function ContactForm() {
    return (
        <div className="contact-form">
            <div className="container">
                <h2 className="title-primary">Остались вопросы? <br /> Оставляйте заявку, мы проконсультируем Вас!</h2>
                <form>
                    <input type="text" name="name" className="form-input"  placeholder="Имя" required />
                    <input type="tel" name="tel" className="form-input"  placeholder="Телефон" required />
                    <div className="text-center">
                        <button className="btn-primary">Отправить</button>
                    </div>
                </form>
            </div>
        </div>
    )
}

export default ContactForm;
