import React, { useState } from 'react';
import img from '../../assets/img/layer1.png';

import declaration1 from '../../assets/img/declaration-1.jpg';
import declaration2 from '../../assets/img/declaration-2.jpg';
import declaration3 from '../../assets/img/declaration-3.jpg';
import declaration4 from '../../assets/img/declaration-4.jpg';
import declaration5 from '../../assets/img/declaration-5.jpg';
import declaration6 from '../../assets/img/declaration-6.jpg';
import declaration7 from '../../assets/img/declaration-7.jpg';

import './About.scss';

function About() {

    const [isHidden, hideShow] = useState(true);

    const onButtonShow = () => {
        if(isHidden) {
            hideShow(false);
        } else {
            hideShow(true);
        }
    }

    return (
        <div id="about" className="about">
            <div className="container">
                <h2 className="title-primary">Качество</h2>
                <p>
                    <b>На постоянной основе</b> производится контроль качества выпускаемой продукции в день отгрузок. Определяется сортность, калибровка и маркировка готовой продукции. Происходит проверка готовой продукции на наличие нитратов и других вредных веществ, в день сбора всех овощей и земляники.
                </p>
                <p>По итогам проверки продукции, превышения допустимых норм нитратов зафиксировано <b>не было</b>, вся продукция соответствует всем допустимым значениям.</p>
                <div className="text-center">
                    <img src={img} className="about-img" alt="background" />
                </div>
                <div className="text-center">
                    <button className="btn-primary" onClick={() => onButtonShow()}>Подробнее</button>
                </div>
                {isHidden ? null : <AboutBottom />}
            </div>
        </div>
    )
}

function AboutBottom() {
    return (
        <React.Fragment>
            <h2 className="title-secondary">Технические показатели</h2>
            <p> В соответствии с требованиями технического регламента Таможенного союза ТР ТС 021/2011 от 01.07.2013г. О безопасности пищевой продукции ( в том числе овощная продукция), подлежит обязательному подтверждению в соответствии формы декларирования. </p>
            <p> ООО «Тепличное» ежегодно декларирует всю свою овощную продукцию в аккредитованной организации ООО «СК». По результатам исследований лабораторией выдаются заключения о виде протоколов испытаний, на основании которых выдаются декларации соответствия, которые подтверждают безопасность и качество производимой продукции. </p>
            <h2 className="title-secondary">Преимущества продукции</h2>
            <p>Сотрудниками агрохимической лаборатории в дни сбора продукции производится исследование на содержание нитратов в продукции.</p>
            <p>На предприятии <b>имеется своя биолаборатория</b> которая производит энтомофагов (полезные насекомые) и биопрепараты для того что бы исключить применение пестицидов для борьбы с вредителями и болезнями, тем самым получать экологически чистую овощную продукцию.</p>
            <h2 className="title-secondary">Декларации качества</h2>
            <div className="row">
                <div className="declarations">
                    <img src={declaration1} alt="Декларация" />
                    <img src={declaration2} alt="Декларация" />
                    <img src={declaration3} alt="Декларация" />
                    <img src={declaration4} alt="Декларация" />
                    <img src={declaration5} alt="Декларация" />
                    <img src={declaration6} alt="Декларация" />
                    <img src={declaration7} alt="Декларация" />
                </div>
            </div>
        </React.Fragment>
    );
}

export default About;
