import React from 'react';
import Catalog from '../Catalog';
import Advantages from '../Advantages';
import About from '../About';
import ContactForm from '../ConcactForm';

import './Main.scss';

import {
    bee
} from '../../assets/images';

import {
    catalogs
} from '../../assets/CatalogsData';


function Main() {    

    return (
        <main>
            <Catalog id="catalog" catalogItemsData={catalogs.main} />
            <BeeBanner />
            <Advantages />
            <About />
            <ContactForm />
        </main>
    )
}

const BeeBanner = () => {
    return (
        <div className="text-center bee">
            <img src={bee} alt="Пчела" className="bee-img" />
            <span>- знак того, что продукты опыляются пчелами</span>
        </div>
    );
}

export default Main
