import React from 'react';
import { Link } from 'react-router-dom';

import './Catalog.scss';


function Catalog(props) {

    const {catalogItemsData} = props;

    const renderItems = (array) => {
        return array.map(one => {
            const { img, title, desc, link } = one;
            return (
                <div className="catalog-item" key={title}>
                    <img className="catalog-img" src={img} alt={title} />
                    <h3 className="catalog-title">{title}</h3>
                    <p className="catalog-desc">{desc}</p>
                    <Link className="catalog-link btn-primary" to={`/${link}/`}>Посмотресть</Link>
                </div>
            )
        });
    }

    const catalogItems = renderItems(catalogItemsData);

    return (
        <section id="catalog" className="catalog">
            <div className="container">
                <h2 className="title-primary">Каталог</h2>
                <div className="row">
                    {catalogItems}
                </div>
            </div>
        </section>
    )
}

export default Catalog;
