import React from 'react';
import ReactDOM from 'react-dom';
import { BrowserRouter as Router, Route } from 'react-router-dom';

import './styles/general.scss';
// Importing components
import Header from './components/Header';
import Main from './components/Main';
import Contacts from './components/Contacts';
import Footer from './components/Footer';
import Products from './components/Products'
import Catalog from './components/Catalog';

import {
    catalogs
} from './assets/CatalogsData';



ReactDOM.render(
    <Router>
        <Route path="/" component={Header} />
        <Route path="/" component={Main} exact />
        <Route path="/product/:productName" render={
            ({match}) => {
                return <Products productName={match.url} />
            }
        } exact />
        <Route path="/catalog/:catalogName" render={
            ({match}) => {
                let result;
                const catalog = catalogs[match.url.match(/[a-zA-Z]+/g)[1]];

                if(catalog) {
                    result = <Catalog catalogItemsData={catalog} />;
                } else {
                    result = (<h2 className="title-primary">Данные не найдены... =\</h2>)
                }

                return result;
            }
        } />
        <Route path="/" component={Contacts} />
        <Route path="/" component={Footer} />
    </Router>
    , document.querySelector('#root'));